import { computed, observable } from "mobx";

const urls = {
	windows: "https://hediet.github.io/git-line-endings/data/windows/data.json",
	mac: "https://hediet.github.io/git-line-endings/data/mac/data.json",
	linux: "https://hediet.github.io/git-line-endings/data/ubuntu/data.json",
};

interface Config {
	os: "mac" | "linux" | "windows";
	text: "auto" | "false" | "true" | "undefined";
	eol: "crlf" | "lf" | "undefined";

	core_autocrlf: "false" | "true";
	core_eol: "crlf" | "lf" | "native";
}

export type LineEnding = "lf" | "crlf" | "mixed";

export interface Entry {
	config: Config;
	mapping: {
		workspace: Record<LineEnding, LineEnding>;
		checkinReplace: Record<LineEnding, LineEnding>;
		checkinNew: Record<LineEnding, LineEnding>;
		checkinPrepend: Record<LineEnding, LineEnding>;
		checkinExtendEmpty: Record<LineEnding, LineEnding>;
		unmodified: Record<LineEnding, LineEnding>;
	};
}

function getGitAttributesConfig(config: Config) {
	const options = new Array<string>();
	if (config.text !== "undefined") {
		if (config.text === "true") {
			options.push(`text`);
		} else if (config.text === "false") {
			options.push(`-text`);
		} else if (config.text === "auto") {
			options.push(`text=auto`);
		}
	}
	if (config.eol !== "undefined") {
		options.push(`eol=${config.eol}`);
	}

	if (options.length > 0) {
		return `*.txt ${options.join(" ")}`;
	} else {
		return "";
	}
}

function getGitArgs(config: Config): string[][] {
	const result = new Array<string[]>();
	result.push(["config", "core.autocrlf", config.core_autocrlf]);
	result.push(["config", "core.eol", config.core_eol]);
	return result;
}

interface BindableValue<T> {
	get(): T;
	set(value: T): void;
}

export interface ActiveBindableValue<T> extends BindableValue<T> {
	readonly enabled: boolean;
}

class Setting<T> implements ActiveBindableValue<T> {
	constructor(defaultValue: T, private isEnabled: () => boolean) {
		this.value = defaultValue;
	}

	@observable
	private value: T;

	get enabled(): boolean {
		return this.isEnabled();
	}

	get(): T {
		return this.value;
	}

	set(value: T): void {
		this.value = value;
	}
}

export class Model {
	@observable private entries: Entry[] | undefined = undefined;

	public readonly os = new Setting<Config["os"]>("windows", () =>
		this.isEnabled("os")
	);
	public readonly text = new Setting<Config["text"]>("auto", () =>
		this.isEnabled("text")
	);
	public readonly eol = new Setting<Config["eol"]>("undefined", () =>
		this.isEnabled("eol")
	);
	public readonly core_autocrlf = new Setting<Config["core_autocrlf"]>(
		"false",
		() => this.isEnabled("core_autocrlf")
	);
	public readonly core_eol = new Setting<Config["core_eol"]>("native", () =>
		this.isEnabled("core_eol")
	);

	private get currentConfig(): Config {
		return {
			os: this.os.get(),
			text: this.text.get(),
			eol: this.eol.get(),
			core_autocrlf: this.core_autocrlf.get(),
			core_eol: this.core_eol.get(),
		};
	}

	public get gitAttributeContent(): string {
		return getGitAttributesConfig(this.currentConfig);
	}

	public get gitConfigCommands(): string {
		return getGitArgs(this.currentConfig)
			.map((c) => `git ${c.join(" ")}`)
			.join("\n");
	}

	private isEnabled(key: keyof Config): boolean {
		if (!this.entries) {
			return false;
		}

		const result = this.entries.filter((d) => {
			return (
				(key === "os" || d.config.os === this.os.get()) &&
				(key === "text" || d.config.text === this.text.get()) &&
				(key === "eol" || d.config.eol === this.eol.get()) &&
				(key === "core_autocrlf" ||
					d.config.core_autocrlf === this.core_autocrlf.get()) &&
				(key === "core_eol" ||
					d.config.core_eol === this.core_eol.get())
			);
		});

		return new Set(result.map((r) => JSON.stringify(r.mapping))).size > 1;
	}

	@computed get selectedEntry(): Entry | undefined {
		if (!this.entries) {
			return undefined;
		}
		const result = this.entries.find((d) => {
			return (
				d.config.os === this.os.get() &&
				d.config.text === this.text.get() &&
				d.config.eol === this.eol.get() &&
				d.config.core_autocrlf === this.core_autocrlf.get() &&
				d.config.core_eol === this.core_eol.get()
			);
		});

		return result;
	}

	constructor() {
		this.init();
	}

	private async init() {
		this.entries = new Array<Entry>().concat(
			...(await Promise.all(
				Object.entries(urls).map(async ([os, url]) => {
					const result = await fetch(url);
					return ((await result.json()) as []).map((v) => {
						(v as any).config.os = os;
						return v as Entry;
					});
				})
			))
		);

		if (
			!this.entries.every(
				(e) =>
					JSON.stringify(e.mapping.checkinPrepend) ===
					JSON.stringify(e.mapping.checkinReplace)
			)
		) {
			alert("checkinReplace != checkinPrepend");
		}

		if (
			!this.entries.every(
				(e) =>
					JSON.stringify(e.mapping.checkinExtendEmpty) ===
					JSON.stringify(e.mapping.checkinNew)
			)
		) {
			alert("checkinExtendEmpty != checkinNew");
		}
	}
}
